@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.links line {
  stroke: rgb(65, 182, 174);
  stroke-opacity: 0.6;
}

.nodes circle {
  stroke: #fff;
  stroke-width: 1.5px;
  border: solid, black, 2em;
}

.container {
  display: grid;
  grid-template-columns: [column1-start] 1100px [column1-end column2-start] auto [column2-end];
  grid-template-areas: "svg | Recipe-box";
}

.graph {
  grid-column-start: column1;
  grid-column-end: column2;
}


.Recipe-search {
  grid-column-start: column2;
  grid-column-end: end;
}
.recipe-search-box {
  position: absolute;
  top: 300px;
  right: 50px;
}

.instructions {
}

.list {
  position: absolute;
  right: 150px;
  /* max-height: 30px; */
}

.recipe-submit-btn {
  flex: 0.5 1;
  margin: 1rem;
  width: 150px;
  height: 45px;
  background-image: linear-gradient(262deg, #ff7854, #fd267d);
  -webkit-transition: 0.25s;
  transition: 0.25s;
  padding: 10px 20px;
  text-align: center;
  font-size: 1em;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
  position: absolute;
  right: 330px;
}

.recipe-clear-btn {
  flex: 0.5 1;
  margin: 1rem;
  width: 150px;
  height: 45px;
  background-image: linear-gradient(262deg, #ff7854, #fd267d);
  -webkit-transition: 0.25s;
  transition: 0.25s;
  padding: 10px 20px;
  text-align: center;
  font-size: 1em;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
  position: absolute;
  right: 330px;
  top: 150px;
}
.recipe-list {
  position: absolute;
  right: 40px;
  top: 600px;
  max-width: 450px;
  list-style-type: none;
}

/* unvisited link */
a:link {
  color: black;
  text-decoration: none;
}

/* visited link */
a:visited {
  color: #ff7854;
  text-decoration: none;
}

/* mouse over link */
a:hover {
  color: #fd267d;
  text-decoration: none;
}

/* selected link */
a:active {
  color: rgb(65, 182, 174);
  text-decoration: none;
}

.recipe-list-header {
  position: absolute;
  right: 300px;
  top: 550px;
}
.submit-btn {
  flex: 0.5 1;
  margin: 1rem;
  width: 50%;
  background-image: linear-gradient(262deg, #ff7854, #fd267d);
  -webkit-transition: 0.25s;
  transition: 0.25s;
  padding: 10px 20px;
  text-align: center;
  font-size: 1em;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
}

