.text {
  font-size: 2em;
}

.form{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  left: 20px;
}

input {
  width: 90%;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
  background-color: white;
  font-size: 1em;
}

input:focus {
  background-color: white;
}

.search-box {
  height: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.Toastify__toast-container {
  -webkit-animation-duration: 5000ms;
          animation-duration: 5000ms;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  opacity: 1;
  position: absolute;
  top: 275px;
  left: 400px;
  font-size: 2em;
  color: rgb(65, 182, 174)
}

.slider {
  font-size: 1em;
}

p {
  margin-top: 0;
}

.App {
  font-family: 'Montserrat', sans-serif;
}

.App-body {
